@import "../scss/variables";
@media (min-width: 1025px) {
 .dark-theme.boxed {
	.page {
		max-width: 1170px;
		margin: 0 auto;
		background: #101329;
		position: relative;
		display: -ms-flexbox;
		display: flex;
		-ms-flex-direction: column;
		flex-direction: column;
		-ms-flex-pack: center;
		justify-content: center;
		box-shadow: rgba(0, 0, 0, 0.3) 0px 5px 0px 0px, rgba(0, 0, 0, 0.3) 0px 5px 25px 0px;
	}
	body {
		background: #3d3d69 !important;
	}
	.horizontalMenu>.horizontalMenu-list>li>a .fa {
		margin-right: 0px !important;
	}
	.navbar {
		max-width: 1170px;
		margin: 0 auto;
	}
	.vh {
		height: 100vh;
	}
	.navbar.land-navbar {
		max-width: 1170px;
		margin: 0 auto;
	}
	.app-sidebar {
		right: auto !important;
	}
	.app .main-header.side-header {
		left: auto !important;
		right: auto !important;
		width: 1170px !important;
	}
	.app.sidebar-mini.sidenav-toggled4 .first-sidemenu {
		left: auto !important;
	}
	.second-sidemenu {
		right: auto !important;
		margin-right: 100px;
		left: auto !important;
	}
	.main-body {
		display: inherit;
	}
	.main-header.side-header {
		left: auto !important;
		right: auto !important;
		position: sticky !important;
	}
	.main-sidebar-hide .side-menu {
		left: auto !important;
	}
	.main-sidebar.side-menu {
		right: auto !important;
	}
	.modal-open .main-sidebar {
		z-index: 999;
	}
	.dark-theme {
		.main-sidebar-sticky {
			box-shadow: none !important;
		}
		.side-header, .main-navbar {
			box-shadow: 0 0px 2px 0 rgb(16, 19, 41) !important;
		}
	}
}
}
@media (min-width: 992px) {
	.sticky-pin {
		top: 0;
		width: 100%;
	}
}
@media (min-width: 1025px){
body.boxed.dark-theme {
    background: #1c1c38 !important;
  }
}  